import React from "react";
import {
  Card,
  Row,
  Col,
  CardBody,
  CardSubtitle,
  CardTitle,
  Button,
  CardText,
} from "reactstrap";
import { Link } from "react-router-dom";
const HomeCard = ({ repository, key }) => {
  return (
    <Link to="/repository_detail">
      <Card className="home_card ml-5 mr-5" key={key}>
        <Row className="home_card_row">
          <Col xl={2} lg={2} md={4} sm={6} xs={12}>
            <img
              top
              width="200"
              height="200"
              src={repository.owner_photo}
              alt=""
            />
          </Col>
          <Col>
            <CardBody>
              <CardTitle tag="h5">{repository.name}</CardTitle>
              <CardSubtitle tag="h6" className="mb-2 text-muted">
                {repository.owner_userName}
              </CardSubtitle>
              <CardText>{repository.description}</CardText>

              <Button className="home_btn">
                {repository.stars + " stars "}
              </Button>

              <Button style={{ marginLeft: "5px" }}>
                {repository.issues + " issues "}
              </Button>

              <span style={{ marginLeft: "5px" }}>
                Time Interval by Owner Name
              </span>
            </CardBody>
          </Col>
        </Row>
      </Card>
    </Link>
  );
};

export default HomeCard;
