import { Request } from "./Request";
import {
  getQueryString,
  showNotificationMessage,
  // getCurrentDate,
} from "../../ReusableCompnents/GeneralFunctions";
import { UPDATE_HOME_PROPS, MERGE_HOME_PROPS } from "../Actions/types";

//list the most starred Github repos that were created in the last 30 days.
export const fetch_Repositries = (query, page) => async (dispatch) => {
  dispatch({
    type: UPDATE_HOME_PROPS,
    payload: [{ prop: "repositoryObj.isLoading", value: true }],
  });

  query.page = page;
  let query_str = getQueryString(query);
  // let url = "/search/repositories" + query_str + "&q=" + getCurrentDate();
  let url = "/search/repositories" + query_str + "&q=created:>2017-10-22";

  await Request()
    .get(url)
    .then((response) => {
      if (response.status === 200) {
        if (response.items !== undefined) {
          if (response.items.length > 0) {
            let result = [];
            for (var count = 0; count < response.items.length; count++) {
              result.push({
                name: response.items[count].name,
                description: response.items[count].description,
                owner_photo: response.items[count].owner.avatar_url,
                owner_userName: response.items[count].owner.login,
                stars: response.items[count].stargazers_count,
                issues: response.items[count].open_issues,
              });
            }

            dispatch({
              type: MERGE_HOME_PROPS,
              payload: [
                { prop: "repositoryObj.repositoryArray", value: result },
              ],
            });
          } else {
            showNotificationMessage("No Result Found");
          }
        }
      } else {
        dispatch({
          type: UPDATE_HOME_PROPS,
          payload: [{ prop: "repositoryObj.hasMore", value: false }],
        });
      }
    })
    .catch((err) => {
      console.log(err);
    })
    .finally(
      dispatch({
        type: UPDATE_HOME_PROPS,
        payload: [{ prop: "repositoryObj.isLoading", value: false }],
      })
    );
};
